.portfolio_wrapper {
  position: relative;
  min-height: 100vh;
  border-top: 1px solid rgba(21, 21, 21, 0.15); 
  border-bottom: 1px solid rgba(21, 21, 21, 0.15);
  display: flex;
    //@include mobile{display: block;}
  > div:nth-child(1) {//left
    width:40%;
    display: block;
    border-right: 1px solid rgba(21, 21, 21, 0.15);
    @include mobile{display: none;}
    
    .portfolio_title_wrap {//inner.sticky
      overflow: hidden;
      height: 100%;

      .portfolio_title_inner{//inner.sticky.bg
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width:100%;
        height: 100%;
        .portfolio_title_content { //side
          position: sticky;
          height: 700px;
          top: 20%;
          left:0;
            .scroll-menu{
              position: absolute;
              top: 0;
              left:-1.2rem;
              transform: translateX(-50%);
              inline-size: 40rem;
              block-size: 40rem;
              background-image: linear-gradient(rgb(224, 224, 224), rgb(175, 175, 175));
              border-radius: 50%;
              overflow: hidden;
              z-index: 100;
                @include tablet {
                  inline-size: 30rem;
                  block-size: 30rem;
                }
                .scrollbar{
                  position: absolute;
                  content: "";
                  top:250px;
                  left:150px;
                  inline-size: 300px;
                  block-size: 700px;
                  background-color: $main-purple;
                  transform-origin: center top;
                  transition: 0.3s all;
                  transform: rotate(220deg);
                }
                .scrollbar.sect01{transform: rotate(220deg);}
                .scrollbar.sect02{transform: rotate(280deg); }
                .scrollbar.sect03{transform: rotate(340deg); }

                .overlay{
                  position: absolute;
                  inset:5px;
                  border-radius: 50%;
                  background-color: #fff;
                }

                .circle-1{
                  position: absolute;
                  inset:150px;
                  border-radius: 50%;
                  z-index: 2002;
                  background: linear-gradient(to right, $main-green, $main-purple);
                    @include tablet{inset: 100px;}
                }
            }
            nav{
              position: absolute;
              left: 20%;
              z-index: 1000;
              //@include tablet{left: 18%;}

              .title_name{
                font-size: 3rem;
                font-family: $font-Eng;
                font-weight: 700;
                @include tablet{font-size: 1.5rem;}
              }
              ul{
                list-style: none;
                @include tablet{text-align: right;}
                li{
                  font-weight: 700;
                  font-size: 1.5rem;
                  padding: 10px;
                  opacity: 0.7;
                  cursor: pointer;
                  transition: all 0.3s;
                }
                li.active {
                  opacity: 1; color: $main-purple;
                }
              }
            }
        }
      }
    }
  } 
  > div:nth-child(2) {//right
    width:60%;
    display: block;
    cursor: pointer;
    @include mobile{width:100%;}

    .portfolio_detail {//right.inner
      list-style-type: none;
      .portfolio_list{
        display: flex;
        //height: 50vh;
        border-bottom:1px solid rgba(21, 21, 21, 0.15);
        @include tablet{height: unset;}
        //@include mobile{display: block;}

          > div:nth-child(1){border-right: 1px solid rgba(21, 21, 21, 0.15);
          }
  
        .portfolio_txt_wrap{
          width: 50%;
          padding: 30px;
          border-left:1px solidrgba(21, 21, 21, 0.15);
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          //@include mobile{ width: 100%;}
          @include mobile{ padding: 10px;}
        
        .name{
            font-size: 2rem;
            font-weight: 700;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-family: $font-Eng;
            @include tablet{ font-size: 1.2rem;}
            @include tablet{ font-size: 1rem;}
              >span {
                @include mobile{display: none;}
              }
          }
          .description{
              padding-top: 20px;
              font-weight: bold;
              @include tablet{ font-size: 0.7rem;padding-top: 10px;}
              }
            
          .keyword{
              display: flex;
              flex-wrap: wrap;
              gap: 8px;
              margin-top: 20px;
                .keyword_item{
                  position: relative;
                  font-weight: 700;
                    &::after{
                      position: absolute;
                      content: "";
                      width: 100%;
                      height: 10px;
                      bottom: -3px;
                      left: 0;
                      background-color: #FF335F;
                      opacity: 0.4;
                      z-index: -1;
                    }

                    
                }
            /*  .keyword_item{
                //background-color: rgba(255, 51, 95,0.7);
                padding: 5px 13px;
                border: 2px solid $main-red;
                border-radius: 999px;
                //font-weight: 700;
                font-size: 0.9rem;
                color: $main-red;

                } */
            }
          
          
          .category{
            background-color: $main-green;
            border-radius: 999px;
            display: inline-block;
            padding: 0 20px;
            line-height: 40px;
            width: 160px;
            text-align: center;
            font-weight: 700;
            margin-top: 20px;
              @include tablet{
                padding: 0 10px;
                line-height: 20px;
                font-size: 0.7rem;
                margin-top: 10px;
                width: 80px;
              }
          }
        }
        .portfolio_img_wrap{
          width: 50%;  
          height: 100%;
          position: relative;
          //@include mobile{ width: 100%;}
        
          img{
            //position: relative;
            width: 100%;
            height: 100%;
            padding: 20px;
            object-fit: contain;
            box-sizing: border-box;
            //box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
          }
          .detail_wrap{
              *{color:#fff;}
            opacity: 0;
            position: absolute;
            top: 50%;
            left: 50%;
            width: calc(100% - 38px);
            height: calc(100% - 40px);
            background-color: rgba(0, 0, 0, 0.9);
            transition: all 0.5s ease;
            transform: translate(-50%, -50%);
            display: flex;
            align-items: center;
            justify-content: center;
            
            .more{
              font-size: 1.2rem;
              border: 4px solid $main-purple;
              border-radius: 999px;
              padding: 10px 25px;
              display: flex;
              align-items: center;
              gap: 20px;
            }
          }
        }
        .portfolio_img_wrap.on{
          .detail_wrap{
            opacity: 1;
          }
        }

      }

      .modal_detail{
        *{color: #fff;}
        position: fixed;
        z-index: 50;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1000px;
        transition: opacity 0.3s;
        opacity: 0;

          .modal_cont{
          
            border-radius: 10px;
            box-shadow: 0 0 10px 0 rgba(0,0,0,0.5);
            background-color:#2e2aa4; /* 2e2aa4,1B1E32  */
            padding: 40px;

            .modal_close{
              position: absolute;
              display: flex;
              right: 9px;
              top: 13px;
              border: none;
              background-color: transparent;
              font-size: 3rem;
            }
            .cont_line{
              width: 120px;
              height: 2px;
              background-color: $main-red;
              margin-bottom: 16px;
              //animation: lineWidth 1s ease-in-out;
            }
              .modal_header_wrap{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
              
                  .cont_name{
                    font-size: 50px;
                    font-family: "Made-medium";
                    font-weight: 700;
                    letter-spacing: 1.5px;
                  }
                  .cont_description{
                    padding-top: 10px;
                    font-size: 1.1rem;
                    letter-spacing: 1.2px;
                  }
              }
              .modal_concept_wrap{
                display: flex;
                flex-direction: column;
                margin: 80px 0;
                  .cont_line{width: 80px;}
                  .cont_concept,.cont_tool{
                    display: flex;
                    align-items: center;
                      >span:nth-child(1){width: 12%; 
                        
                        font-size: 20px;
                        font-family: $font-Eng;
                        }
                      >span:nth-child(2){width: 12%; 
                        color: #ede7e7;
                        
                      }
                }
                  .cont_tool{
                    padding-top: 16px;
                    .cont_tool_item{
                      list-style: none;
                      display: flex;
                      gap: 8px;
                      >span {color: #ede7e7;
                      }
                    }
                  }
                

            }
            .modal_btn_wrap{
              display: flex;
              gap: 20px;
              button{
                background-color: $main-red;
                border-radius: 999px;
                border: none;
                padding: 0 20px;
                line-height: 40px;
                width: 130px;
                text-align: center;
                font-size: 1.1rem;
                font-family: $font-Eng;

                  @include tablet{
                    padding: 0 10px;
                    line-height: 20px;
                    font-size: 0.7rem;
                    margin-top: 10px;
                    width: 80px;
                  }
              }
            }
            .modal_detail_wrap{
              line-height: 1.8;
              margin-top: 50px;
                .cont_line{width: 50px;}
                .cont_script_detail{width: 50%;
                font-weight: 400;
                }
            }
          }
          .modal_bg{
            position: absolute;
            top: 150px;
            right: -150px;
            width: 800px;
            height: 800px;
            img{width: 100%;}
          }
          .modal_pc{
            position: absolute;
            width: 500px;
            height: 500px;
            right: 0;
            bottom: 0;
            img{width: 100%;}
          }
      }
      .modal_detail.show{
        opacity: 1;
      }
    }
  }
}

@keyframes lineWidth {
  0% {width: 0%;}
  100%{width:100%}
}




