.wrapper{max-width: 1200px; 
  margin: 200px auto; 
  border-top: 1px solid rgba(21, 21, 21, 0.15); 
  display: flex;
  @include tablet{ margin: 300px 0 100px 0;}
  @include mobile{margin-top: 370px; margin-bottom: 100px;}

  .about_left{flex:1; 
    border-right:  1px solid rgba(21, 21, 21, 0.15); 
    text-align: center; 
    padding: 17px; 
    max-width: 272px; 
    width: 100%; 
    font-size: 1.6rem; 
    line-height: 3.4rem;
    p {background-color:$main-purple; 
      border-radius: 30px; 
      display: inline-block;
      font-size: 1.5rem;
      display: flex; 
      align-items: center;
      justify-content: space-around; 
      color: #fff;
    
    @include mobile{line-height: 1.1; display: block; padding: 15px;
      span{display: none;}
    }
    }
  }

  .about_right {position: relative; 
    flex:2; 
    padding: 100px 70px;
      @include mobile {padding: 50px 40px;}
    

    &:before{
      position: absolute; 
      top:50px;
      left:40px; 
      content: ""; 
     /*  background: url('../../img/image6.png') no-repeat; */
      width: 40px; 
      height: 40px;
      filter: contrast(0.8);
        @include mobile{top:15px;}
    }
  
  p {font-size: 2rem;
    @include tablet {font-size: 1.5rem;}
    @include mobile {font-size: 1rem;}
  }
  }
}
