.skill_wrap{
  background-color: #000;
  padding: 50px 0;
  @include mobile {padding: 20px 0;}

  .skill_inner{
    display: flex;
    margin: auto;
    justify-content: space-between;
    max-width: 1480px;
      @include mobile {display: block;}

      .skill_left{
        width: 30%;
        border-right: 1px solid rgba(236, 236, 236, 0.4);
        text-align: right;
        padding: 20px 80px;
          @include tablet{padding: 20px 30px;text-align: left;}
          @include mobile { width: 100%;}
        
        h2{
          color: #fff;
          font-family: "Made-medium";
          font-size: 3.5rem;
          margin-top: 10px;
          @include tablet{font-size: 3rem;}
          @include mobile{font-size: 2rem;
          }
        }
        >span {
          background-color: #FF335F;
          border-radius: 30px;
          padding: 5px 30px;
          font-size: 1.5rem;
          color: #fff;
          line-height: 40px;
          display: inline-block;
          @include tablet{padding: 5px 20px;}
          .arrow{
            padding-left: 50px;
          }
          }
      }
    
      .skill_right {position: relative; flex:1;
        display: flex; 
        flex-wrap: wrap; 
        gap: 30px;
        padding: 30px;
          @include tablet{justify-content: center;}
          @include mobile{
            gap: 20px;
            padding: 10px;
          }

        > div{
        flex: 1;
        padding: 15px;
        background-color: transparent;
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.15);
        transition: 0.3s ease-in-out;
          &:hover{transform: scale(1.1);}

          @include mobile{ 
            //width: 20%;
            width: 120px;
            height: 120px;
            text-align: center;
          }

          img{
            @include mobile{text-align: center;}
          }
          .skill_name{
            color: #fff;
            text-align: center;
            padding-bottom: 50px;
            font-size: 1.5rem;
            font-family: 'Made-medium';
          }
          .skill_item{
            display: flex;
            padding: 0 0 25px 25px;
            gap: 20px;
            align-items: center;

              span{color:#fff;font-size: 1.2rem;}
            
          }
        }
      
      }
    }
  }



