.contact_wrap{
  height: 500px;
  background-color: #333;
}

.modal_container{
  position: fixed;
  z-index: 999;
  left: auto;
  top: auto;
  width: 350px;
  height: 600px;
  right: 60px;
  bottom: 40px;
  background-color: hsla(30, 7%, 94%, 0.5);
    backdrop-filter: blur(20px) saturate(1.7);
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
  border-radius: 20px;
  overflow: hidden;
  transition: 0.3s ease;
  opacity: 0;

  .modal_header{
    background-color: #595AFF;
    backdrop-filter: blur(20px) saturate(1.7);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    line-height: 1.2;
    position: absolute;
    z-index: 999;
    width: 100%;

      .modal_title{
        display: flex;
        .avater{
          margin-right: 15px;
          img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
          }
        }
        .header_cont{
          .header_title{color: #fff;font-weight:700;}
          .header_subtitle{color: #fff;font-size: 0.9rem;
            margin-top: 5px;}
        }
      }

      .modal_close{
        border:none;
        background-color: transparent;
        font-size: 1.5rem;
        color: #fff;
        display: flex;
        padding: 10px;
        border-radius: 10px;
        transition: 0.3s ease;
          &:hover{
            background-color: darken($color: #ffffff50, $amount: 30);
          }
      }

    
  }
  .modal_inner{
    width: 100%;
    position: absolute;
    top: 74px;
    overflow-y: scroll;
    height: 526px;
    z-index: 999;
    padding: 10px;
    /*  &::-webkit-scrollbar {
        width: 10px;
        background-color: black;
      }
      &::-webkit-scrollbar-thumb {
        background-color: $main-green;
      }
      & .container::-webkit-scrollbar-track {
        background-color: grey;
      } */

      .bubble_box{
        
        
        .bubble_text{
          position: relative;
          display: inline-block;
          padding: 16px;
          border-radius: 16px;
          background: #fff;
          color: $main-purple;
          margin-bottom: 6px;
          word-break: break-word;
          box-shadow: 2px 0px 5px rgba(0,0,0,0.3);
        }

        .bubble_select{
          position: relative;
          display: inline-block;
          padding: 16px;
          border-radius: 16px;
          background: $main-purple;
          box-shadow: 2px 0px 5px rgba(0,0,0,0.3);
          cursor: pointer;
          transition: all .2s ease-in-out;
          margin-bottom: 6px;
            &:hover{background-color: lighten($main-purple, 10%);}

            .select_cont{color: #fff;}
        }
      }

      .right{
        text-align: right;
        margin: 8px 0;
        
        
        .bubble_text{
          background-color: #94f3d0;
          color: #000;
          right: 0;
          max-width: 250px;
        }
      }
  }
}
.modal_container.show{
  opacity: 1;
}
